#gameMain {
  position: relative;
  width: 100%;
  height: 100%;
  /* background-color: #ccc; */
  z-index: 1;
  font-size: 18rem;
}
#gameMain .background {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#gameMain .background svg {
  width: 100%;
  height: 100%;
}
#gameMain .scene {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#gameMain .scene0 .joinGameButton {
  position: absolute;
  top: 50%;
  left: 26rem;
  right: 26rem;
  font-size: 36rem;
  padding: 29rem 0 20rem;
  text-align: center;
  color: #FFFFFF;
  background-color: #000000;
  border-radius: 4px;
  text-transform: uppercase;
  transform: translateY(-50%);
  touch-action: manipulation;
}
.scene .centerText {
  position: absolute;
  top: 50%;
  left: 26rem;
  right: 26rem;
  font-size: 18rem;
  text-align: center;
  transform: translateY(-50%);
  font-style: italic;
}
.scene2 .option {
  position: absolute;
  width: 290rem;
  height: 290rem;
  border-radius: 100%;
  font-size: 54rem;
  /* border: 1px solid #000; */
  display: flex;
  justify-content: center;
  align-items: center;
  left: 50%;
  text-transform: uppercase;
}
.scene2 .option1 {
  box-shadow: 0 0 10px rgba(0,0,0, 0.12);
  bottom: 50%;
  transform: translate(-50%, -48rem);
}
.scene2 .option2 {
  background-color: #000000;
  top: 50%;
  transform: translate(-50%, 48rem);
  color: #FFFFFF;
}
.scene4 .resultTitle {
  font-family: "SephoraScript-Editor";
  font-size: 28rem;
  margin-bottom: 1rem;
}
.scene4 .resultText {
  font-size: 100rem;
  text-transform: uppercase;
}
.scene5 .centerText {
  font-size: 269rem;
}
.scene6 .centerText {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #FFFFFF;
  font-size: 66rem;
}
.scene6 .centerText .mobileIcon {
  width: 90rem;
  margin-bottom: 40rem;
}
.scene6 .centerText .shakeEffect {
  width: 35rem;
  height: 23rem;
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  visibility: hidden;
}
.scene6 .centerText .shakeEffect.shakeEffect1 {
  transform: translate(-50rem, 30rem);
}
.scene6 .centerText .shakeEffect.shakeEffect2 {
  transform: translate(15rem, -170rem) rotate(180DEG);
}
